<template>
	<div class="userinfo-box setting">
		<li class="clearfix">
			<label class="info-item"><i class="iconfont icontel icon-bangdingshouji"></i>登录手机</label>
			<div class="infocontent">{{ infotel }}</div>
			<div class="action"><a @click="editername = true">修改</a><p>一个手机号只能绑定一个企鹅号，验证后可作为登录ID</p></div>
			<Modal  title="修改手机号" v-model="editername"  :mask-closable="false" @on-ok="subedittel" class-name="vertical-center-modal" width="450px" :modal_loading="modal_loading">
				<p slot="header" style="text-align:left">
					<Icon type="android-apps"></Icon>
		            <span>修改手机号</span>
		        </p>
		        <div class="editername-box clearfix">
		        	<div class="edit-item">
		        		<label>原手机号码:</label>
		        		<div class="edit-item-info">183****1882</div>
		        	</div>
		        	<div class="edit-item">
		        		<label>新手机号码:</label>
		        		<div class="edit-item-info">
		        			<Input v-model="newtelnumber" size="large" :ref="telinput" placeholder="" style="width: 270px;" :autofocus="focusState"></Input>
		        		</div>
		        	</div>
		        	<div class="edit-item">
		        		<label>短信验证码:</label>
		        		<div class="edit-item-info">
		        			<Input v-model="infocode" size="large" placeholder="" style="width: 130px;margin-right: 20px;"></Input>
		        			<Button type="primary" size="small"  v-if="!sendMsgDisabled" @click="sendtelCode">发送短信验证码</Button>
		        			<Button type="primary" size="small" v-if="sendMsgDisabled" disabled>{{time+'秒后重新获取'}}</Button>
		        		</div>
		        	</div>
		        </div>
		        <div slot="footer" style="text-align: center;">
		            <Button type="primary" size="large"   @click="subedittel">确定</Button>
		        </div>
		    </Modal>
		</li>
		<li class="clearfix">
			<label class="info-item"><i class="iconfont iconemail icon-youxiang1"></i>登录邮箱</label>
			<div class="infocontent">150018405@qq.com</div>
			<div class="action"><a @click="mailbox = true">修改</a><p>一个月内只能申请修改一次</p></div>
			<Modal  title="修改邮箱" v-model="mailbox" :mask-closable="false" class-name="vertical-center-modal" width="450px">
				<p slot="header" style="text-align:left">
					<Icon type="android-apps"></Icon>
		            <span>修改邮箱</span>
		        </p>
		        <div class="editername-box clearfix">
		        	<div class="edit-item">
		        		<label>原邮箱账号:</label>
		        		<div class="edit-item-info">yanceykang@163.com</div>
		        	</div>
		        	<div class="edit-item">
		        		<label>新邮箱账号:</label>
		        		<div class="edit-item-info">
		        			<Input v-model="mailenumber" size="large" :ref="maileinput" placeholder="" style="width: 270px;" :autofocus="focusState"></Input>
		        		</div>
		        	</div>
		        </div>
		        <div slot="footer" style="text-align: center;">
		            <Button type="primary" size="large" @click="editermail">发送激活链接</Button>
		        </div>
		    </Modal>
		</li>
		<li class="clearfix">
			<label class="info-item"><i class="iconfont iconqq icon-qq1"></i>登录QQ</label>
			<div class="infocontent">150018405</div>
			<div class="action"><a @click="bingqq">解绑</a><p>登录QQ作为管理员QQ，享受多重特权</p></div>
		</li>
		<li class="clearfix">
			<label class="info-item"><i class="iconfont iconweixin icon-weixin1"></i>登录微信</label>
			<div class="infocontent">暂无</div>
			<div class="action"><a @click="weixinbox = true">绑定</a><p>绑定后可使用微信扫码登录腾讯内容开放平台</p></div>
			<Modal  title="绑定微信" v-model="weixinbox" :mask-closable="false" class-name="vertical-center-modal" width="350px">
				<p slot="header" style="text-align:left">
					<Icon type="android-apps"></Icon>
		            <span>绑定微信</span>
		        </p>
		        <div class="editername-box clearfix">
		        	<img src="http://image.woshipm.com/wp-files/2017/09/rpPRjFM56semOSrrJuWf.png">
		        </div>
		        <div slot="footer" style="text-align: center;">
		        </div>
		    </Modal>
		</li>
	</div>
</template>

<script>
export default {
  data () {
    return {
      editername: false,
      weixinbox: false,
      qqbox: false,
      mailbox: false,
      infotel: '18388888892',
      infoname: '科技男',
      newname: '',
      newtelnumber: '',
      focusState: false,
      telinput: 'newtelnumber',
      sendMsgDisabled: false,
      time: 10,
      modal_loading: true,
      infocode: '',
      mailenumber: '',
      maileinput: 'maileinput'
    }
  },
  methods: {
    sendtelCode () {
      if (this.newtelnumber === '') {
        this.$refs[this.telinput].focus()
        this.$Message.error('请输入手机号码！')
        return false
      }
      if (this.newtelnumber === this.infotel) {
        this.$refs[this.telinput].focus()
        this.$Message.error('手机号码和原有的相同！')
        return false
      } else {
        if (!(/^1[3|4|5|8][0-9]\d{4,8}$/.test(this.newtelnumber))) {
          this.$Message.error('手机号码格式不正确！')
          this.$refs[this.telinput].focus()
          return false
        } else {
          this.$http.post('/static/js/data.json?page=', {user: this.infoname}).then(function (data) {
            let me = this
            me.sendMsgDisabled = true
            let interval = window.setInterval(function () {
              console.log(me.time)
              if ((me.time--) <= 0) {
                me.time = 10
                me.sendMsgDisabled = false
                window.clearInterval(interval)
              }
            }, 1000)
          }, function (error) {
            this.$Message.error('验证码请求失败！')
            console.log(error)
          })
        }
      }
    },
    subedittel () {
      if (this.newtelnumber === '') {
        this.$refs[this.telinput].focus()
        this.$Message.error('请输入手机号码！')
        return false
      }
      if (this.infocode === '') {
        this.$Message.error('请输入验证码！')
        return false
      }
    },
    editermail () {
      if (!(/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(this.mailenumber))) {
        this.$Message.error('邮箱格式不正确！')
        this.$refs[this.maileinput].focus()
        return false
      } else {
        this.$Notice.success({
          title: '邮件发送成功',
          desc: ' 激活链接已发送至 ' + this.mailenumber + '，请登录<a href="">QQ邮箱</a>，点击激活链接进行激活！',
          duration: 10
        })
        this.$Message.error('请输入邮箱账号！')
      }
    },
    bingqq () {
      this.$Notice.error({
        title: '绑定QQ 提醒',
        desc: ' 暂时不能解除绑定的QQ号码，后续会支持',
        duration: 5
      })
    }
  }
}
</script>

<style>
</style>


